import { css, excludeProps, modifier, styled } from '@lokalise/styled';

import { value } from '../../utils/theme';

export type RectangleProps = {
	/**
	 * Center the Rectangle
	 */
	centered?: boolean;
	/**
	 * Set the height of Rectangle.
	 */
	height: number;
	/**
	 * Set the width of Rectangle. Can use percentage value.
	 */
	width: number | string;
};

export const Rectangle = styled.div.withConfig({
	shouldForwardProp: excludeProps(['height', 'width', 'centered']),
})<RectangleProps>`
	height: ${({ height }) => height}px;
	width: ${({ width }) => (typeof width === 'string' ? width : `${width}px`)};
	border-radius: ${value('radius.m')};
	background-color: ${value('color.background.disabled')};
	${modifier(
		'centered',
		css`
			margin: 0 auto;
		`,
	)};
`;

Rectangle.displayName = 'Rectangle';
